// mui-components & hooks
import { Box, FormControl, MenuItem, Select, useMediaQuery } from "@mui/material";

// mui-icons
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';

// constants
import { SPEECH_BUBBLE_DARK_SIDE, SPEECH_BUBBLE_LIGHT_SIDE } from "../../constants/constants";

export const SelectLang = ({lang, setLang, stop}) => {

    const onSelectChange = e => {
      try {
        stop();
      } catch {}

      setLang(e.target.value);
    };

    const is1024px = useMediaQuery('@media screen and (max-width: 1024px)');

    const selectStyles = {
      color: SPEECH_BUBBLE_DARK_SIDE, 
      '.MuiOutlinedInput-notchedOutline': { 
        borderColor: SPEECH_BUBBLE_LIGHT_SIDE,
        transition: 'border 200ms ease',
      },
      '.MuiSelect-select': {
        padding: is1024px ? '12px 24px 12px 10px !important' : '16px 32px 16px 14px !important',
        fontSize: is1024px ? '14px !important' : '16px !important',
      },
    };

    const iconStyles = {
      color: SPEECH_BUBBLE_DARK_SIDE, 
      pointerEvents: 'none',
      userSelect: 'none', 
      position: 'absolute', 
      right: 12,
      fontSize: is1024px ? '20px' : '24px',
    };

    const Icon = () => <KeyboardDoubleArrowDownIcon sx={iconStyles} />

    return <Box sx={{ width: is1024px ? '240px' : '280px' }}>
            <FormControl fullWidth>
                <Select
                    IconComponent={Icon}
                    value={lang}
                    onChange={onSelectChange}
                    sx={selectStyles}
                >   
                    <MenuItem value={'bn-BD'}>Bengali (Bangladesh)</MenuItem>
                    <MenuItem value={'bg-BG'}>Bulgarian (Bulgaria)</MenuItem>
                    <MenuItem value={'cs-CZ'}>Czech (Czech Republic)</MenuItem>
                    <MenuItem value={'da-DK'}>Danish (Denmark)</MenuItem>
                    <MenuItem value={'nl-NL'}>Dutch (Netherlands)</MenuItem>
                    <MenuItem value={'en-AU'}>English (Australia)</MenuItem>
                    <MenuItem value={'en-IN'}>English (India)</MenuItem>
                    <MenuItem value={'en-SG'}>English (Singapore)</MenuItem>
                    <MenuItem value={'en-GB'}>English (United Kingdom)</MenuItem>
                    <MenuItem value={'en-US'}>English (United States)</MenuItem>
                    <MenuItem value={'fr-CA'}>French (Canada)</MenuItem>
                    <MenuItem value={'fr-FR'}>French (France)</MenuItem>
                    <MenuItem value={'de-DE'}>German (Germany)</MenuItem>
                    <MenuItem value={'hi-IN'}>Hindi (India)</MenuItem>
                    <MenuItem value={'hu-HU'}>Hungarian (Hungary)</MenuItem>
                    <MenuItem value={'id-ID'}>Indonesian (Indonesia)</MenuItem>
                    <MenuItem value={'it-IT'}>Italian (Italy)</MenuItem>
                    <MenuItem value={'ja-JP'}>Japanese (Japan)</MenuItem>
                    <MenuItem value={'kn-IN'}>Kannada (India)</MenuItem>
                    <MenuItem value={'km-KH'}>Khmer (Cambodia)</MenuItem>
                    <MenuItem value={'ko-KR'}>Korean (South Korea)</MenuItem>
                    <MenuItem value={'ml-IN'}>Malayalam (India)</MenuItem>
                    <MenuItem value={'mr-IN'}>Marathi (India)</MenuItem>
                    <MenuItem value={'pt-BR'}>Portuguese (Brazil)</MenuItem>
                    <MenuItem value={'ru-RU'}>Russian (Russia)</MenuItem>
                    <MenuItem value={'rw-RW'}>Kinyarwanda (Rwanda)</MenuItem>
                    <MenuItem value={'ss-latn-za'}>Swati (South Africa)</MenuItem>
                    <MenuItem value={'st-ZA'}>Southern Sotho (South Africa)</MenuItem>
                    <MenuItem value={'es-ES'}>Spanish (Spain)</MenuItem>
                    <MenuItem value={'es-US'}>Spanish (United States)</MenuItem>
                    <MenuItem value={'sv-SE'}>Swedish (Sweden)</MenuItem>
                    <MenuItem value={'ta-IN'}>Tamil (India)</MenuItem>
                    <MenuItem value={'te-IN'}>Telugu (India)</MenuItem>
                    <MenuItem value={'tn-latn-za'}>Setswana (South Africa)</MenuItem>
                    <MenuItem value={'ts-ZA'}>Tsonga (South Africa)</MenuItem>
                    <MenuItem value={'ve-ZA'}>Venda (South Africa)</MenuItem>
                </Select>
            </FormControl>
        </Box>
};