// react
import { useState, useEffect } from "react";

// components
import { TextToSpeechUtils } from "../TextToSpeechUtils/TextToSpeechUtils";

// mui-components & hooks
import { Box, TextField, useMediaQuery } from "@mui/material";

// hooks
import { useSpeechSynthesis } from 'react-speech-kit';

// constants
import { SPEECH_BUBBLE_LIGHT_SIDE } from "../../constants/constants";

// mui-styled
import styled from "@emotion/styled";

const TextToSpeechBox = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'column',
});

let textToSpeechInpIsFocused = false;

export const TextToSpeech = ({setMode}) => {

    const [value, setValue] = useState('');
    const [voice, setVoice] = useState('');

    const { speak, speaking, voices, cancel, supported } = useSpeechSynthesis();

    useEffect(() => {
      if (voices.length) setVoice(voices[0].name);
    }, [voices.length]);

    const onFieldChange = e => setValue(e.target.value);
    
    const onFocus = e => {
      textToSpeechInpIsFocused = true;
      const val = e.target.value;
      e.target.value = '';
      e.target.value = val;     
    };

    const onBlur = () => textToSpeechInpIsFocused = false;

    const is1024px = useMediaQuery('@media screen and (max-width: 1024px)');
    const is768px = useMediaQuery('@media screen and (max-width: 1024px)');

    const inputProps = {
      readOnly: false,
      inputProps: {
        style: {
          color: SPEECH_BUBBLE_LIGHT_SIDE,
          fontFamily: `"Nunito", "roboto", "sans-serif`,
          width: '60vw',
          textAlign: 'center',
          overflow: 'auto',
          maxHeight: '30vh',
          fontSize: is768px ? '16px' : is1024px ? '18px' : '20px',
        }
      }
    };

      if (supported) {
        return <TextToSpeechBox>
        <TextToSpeechUtils setMode={setMode} speaking={speaking} speak={speak} value={value} voices={voices} voice={voice} setVoice={setVoice} cancel={cancel} />
        <TextField
          multiline
          autoFocus={textToSpeechInpIsFocused}
          InputProps={inputProps}
          defaultValue={value}
          placeholder="Write here a text you want to listen"
          onChange={onFieldChange}
          onFocus={onFocus}
          onBlur={onBlur}
        />
      </TextToSpeechBox>
      }
};